export default {
	data() {
		return {
			idDisabled: true
		}
	},
	onLoad() {
		
	},
	methods: {
		confirm(){
			uni.showToast({
				title: "活动码不存在",
				icon: "none"
			})
		},
		input(e) {
			if(e.detail.value !== ""){
				this.idDisabled = false;
			}else{
				this.idDisabled = true;
			}
		}
	}
}
